import { useEffect, useState } from "react";
import "./HelloWidget.scss";

export default function HelloWidget(props) {
  let stringDate;
  let todaysDate;
  let date = new Date();
  const [time, setTime] = useState(date.toUTCString().split(" ")[4]);

  // useEffect(() => {
  //   const startTimer = setInterval(
  //     () => setTime(Date.now()),
  //     1000
  //   );
  //   return () => {
  //     clearInterval(startTimer);
  //   };
  // }, []);

  getDate();

  function getDate() {
    let days = {
      Mon: "Monday",
      Tue: "Tuesday",
      Wed: "Wednesday",
      Thu: "Thursday",
      Fri: "Friday",
      Sat: "Saturday",
      Sun: "Sunday",
    };

    let months = {
      Jan: "January",
      Feb: "February",
      Mar: "March",
      Apr: "April",
      May: "May",
      Jun: "June",
      Jul: "July",
      Aug: "August",
      Sept: "September",
      Oct: "October",
      Nov: "November",
      Dec: "December",
    };

    let day = date.toUTCString().split(" ")[1];
    let month = months[date.toUTCString().split(" ")[2]];
    let year = date.toUTCString().split(" ")[3];

    stringDate = days[date.toUTCString().split(",")[0]];

    todaysDate = day + " " + month + " " + year;
  }

  return (
    <div className="helloWidget">
      <div className="helloImage">
        <img
          src={require("../../../images/icons/hexagon_frame.png")}
          alt="It is night"
          className="img1"
        />
        <img
          src={require("../../../images/icons/moon.png")}
          alt="It is night"
          className="img2"
        />
      </div>
      <div className="helloText">
        <h1>Good Afternoon, {props.name.split(" ")[0]}.</h1>
        <p>
          {stringDate}, {todaysDate}
        </p>
      </div>
      <div className="helloTime">
        <h2>{time}</h2>
      </div>
    </div>
  );
}
